import React from 'react';
import PropTypes from 'prop-types';
import { Avatar } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

const getNumberBackgroundColor = ({ number }) => {
  if (number <= 10) {
    return '#fbc400';
  }

  if (number <= 20) {
    return '#69c8f2';
  }

  if (number <= 30) {
    return '#ff7272';
  }

  if (number <= 40) {
    return '#aaa';
  }

  return '#b0d840';
};

const useStyles = makeStyles({
  root: {
    margin: '2px',
    width: '30px',
    height: '30px',
    fontSize: '1rem',
    backgroundColor: ({ number }) => getNumberBackgroundColor({ number }),
  },
});

const Numbers = ({ number }) => {
  const classes = useStyles({ number });
  return <Avatar className={classes.root}>{number}</Avatar>;
};

Numbers.propTypes = {
  number: PropTypes.number.isRequired,
};

export default Numbers;
