import React from 'react';
import PropTypes from 'prop-types';
import {
  Dialog, DialogTitle, Grid, Button, DialogContent, DialogActions,
} from '@material-ui/core';
import Numbers from './Numbers';

const ResultModal = ({ open, lottoNumbers, handleClose }) => (
  <Dialog aria-labelledby="result-modal-title" open={open} onClose={handleClose}>
    <DialogTitle id="result-modal-title">
      <Grid container justify="center" alignItems="center">
        풍수지리 패턴 분석결과
      </Grid>
    </DialogTitle>

    <DialogContent>
      {lottoNumbers.map(numbers => (
        <Grid key={numbers} container justify="center" alignItems="center">
          {numbers.map(number => (
            <Numbers key={number} number={number} />
          ))}
        </Grid>
      ))}
    </DialogContent>

    <DialogActions>
      <Button variant="contained" color="primary" fullWidth onClick={handleClose}>
        닫기
      </Button>
    </DialogActions>
  </Dialog>
);

ResultModal.propTypes = {
  open: PropTypes.bool.isRequired,
  lottoNumbers: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.number)).isRequired,
  handleClose: PropTypes.func.isRequired,
};

export default ResultModal;
