import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles({
  root: {
    position: 'fixed',
    width: '100%',
    height: '100%',
  },
});

const isLocalhost = Boolean(
  window.location.hostname === 'localhost'
    // [::1] is the IPv6 localhost address.
    || window.location.hostname === '[::1]'
    // 127.0.0.1/8 is considered localhost for IPv4.
    || window.location.hostname.match(/^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/),
);

const Panorama = () => {
  const classes = useStyles();

  useEffect(() => {
    if (isLocalhost) {
      const paranomaStyle = document.getElementById('panorama').style;
      paranomaStyle.backgroundColor = 'grey';
      paranomaStyle.width = '100%';
      paranomaStyle.height = '100%';
      paranomaStyle.position = 'fixed';
    } else {
      /* eslint-disable */
      const panorama = new window.naver.maps.Panorama('panorama', {
        panoId: 'C9p1IfEWYtci7irXSJRJSg==', // 한라산
        zoomControl: true,
        size: {
          width: window.innerWidth,
          height: window.innerHeight,
        },
      });
      /* eslint-enable */
    }
  }, []);

  return (
    <div className={classes.root}>
      <div id="panorama" />
    </div>
  );
};

export default Panorama;
