import React, { useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import Fab from '@material-ui/core/Fab';
import Fingerprint from '@material-ui/icons/Fingerprint';
import { sampleSize, range } from 'lodash';
import ResultModal from './ResultModal';

const generateLottoNumbers = () => sampleSize(range(1, 46), 6).sort((a, b) => a - b);

const useStyles = makeStyles({
  root: {
    position: 'absolute',
    margin: 'auto',
    left: 0,
    right: 0,
    top: -29,
  },
});

const GenerateButton = () => {
  const classes = useStyles();
  const [lottoNumbers, setLottoNumbers] = useState([]);
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    const generatedLottoNumbers = [...Array(5)].map(() => generateLottoNumbers());
    setLottoNumbers(generatedLottoNumbers);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Fab
        color="primary"
        className={`${classes.root}`}
        aria-label="Generate"
        onClick={handleClick}
      >
        <Fingerprint className="animated infinite pulse" fontSize="large" />
      </Fab>
      <ResultModal open={open} lottoNumbers={lottoNumbers} handleClose={handleClose} />
    </>
  );
};

export default GenerateButton;
